/**
 * Add an string or buffer to IPFS
 * @param {String|Buffer} `input` a single string or buffer
 * @param {Function} `callback` a callback, with (error, ipfsHash String)
 * @callback {String} `ipfsHash` returns an IPFS hash string
 */
export const add = (input, name, queryParams) => {
  console.log('add was called')
  var data = ((typeof input === 'object' && input.isBuffer) ? input.toString('binary') : input);
  console.log('add was called - 2')
  var boundary = createBoundary(data);
  console.log('add was called - 3')
  var payload = `--${boundary}\r\nContent-Disposition: form-data; name="path"\r\nContent-Type: application/octet-stream\r\n\r\n${data}\r\n--${boundary}--`;

  console.log('in the add')
  return fetch('https://ipfs.infura.io:5001/api/v0/add' + queryParams, {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'multipart/form-data; boundary=${boundary}'
    },
    body: payload,
  })
    .then(r => r.json())
    .then(data => data)
  // return sendAsync({
  //   jsonParse: true,
  //   accept: 'application/json',
  //   uri: '/add',
  //   takeHash: true,
  //   payload, boundary,
  // });
};

export const pin = (ipfsHash) => fetch(
  'https://ipfs.infura.io:5001/api/v0/pin/add?arg=/ipfs/' + ipfsHash
)
  .then(r => r.json())
  .then(data => data)

export const cat = (ipfsHash) => fetch(
  'https://ipfs.infura.io:5001/api/v0/cat?arg=' + ipfsHash
)
  .then(r => r.text())
  .then(data => data)

